@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
    font-size: 1em;
}

html {
    scroll-behavior: smooth;
}

//************* NAV ****************
//**********************************
.nav {
    position: relative;
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    background: rgba(8, 89, 121, 0.929);
    box-shadow: 0px 0px 8px 0.9px black;
    border: none;
    position: fixed;
    padding: 0.5em;
    z-index: 1;
    top: 0;

    .logo>a>svg {
        position: relative;
        display: flex;
        object-fit: cover;
        justify-content: center;
        align-items: center;
        z-index: 999;
        width: 150px;
        height: 50px;
        top: 0.5em;

        @media (min-width: 480px) {
            top: 0;
        }
    }

    .nav-links-container {
        position: relative;
        display: flex;
        margin-right: 1em;
        justify-content: center;
        justify-content: space-between;
        align-items: center;

        a {
            text-decoration: none;
            color: rgb(255, 255, 255);
            font-size: 1em;
            transition: all 0.4s ease;
            display: none;
            cursor: pointer;
            margin-left: 2.2em;

            @media (min-width:480px) {
                display: block;
                font-size: 0.6em;
            }

            @media (min-width:768px) {
                display: block;
                font-size: 0.9em;
            }
        }

        a:hover {
            transform: scale(1.05);
            transition: all 0.4s ease;
        }
    }

    //****** NAV-TOGGLE **********************
    //****************************************
    .nav-toggle {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 0em;
        background: none;
        width: 4em;
        height: 4em;
        border: none;
        transition: all 0.4s ease;
        cursor: pointer;

        @media (min-width:480px) {
            display: none;
        }
    }
}

//********* MOBILE-NAV **********
//*******************************
.mobile-nav {
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
    display: none;
    background: white;
    left: 0;
    top: 100%;

    &.show-mobile-nav {
        display: block;
    }

    .links {
        position: relative;
        display: flex;
        text-decoration: none;
        color: rgb(218, 229, 255);
        transition: all 0.04s ease;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #00ddff3a;
        background: #014e5aee;
        width: 100%;
        padding: 0.5em;
    }

    .links:hover {
        transform: scale(1.04);
        transition: all 0.04s ease;
        cursor: pointer;
    }
}

//************** HERO ************************
//********************************************
section {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-image: url("../images/hero.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    overflow: hidden;
    scroll-behavior: smooth;

    @media (min-width:1024px) {
        background-size: cover;
    }

    article {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        line-height: 0.5em;

        h1 {
            font-weight: bold;
            font-size: 40px;
            color: #00DCFF;

            @media (min-width:768px) {
                font-size: 55px;
            }

            @media (min-width:1024px) {
                font-size: 60px;
            }

            span {
                color: #ffffff;
            }
        }

        h4 {
            font-size: 18.5px;
            font-style: normal;
            color: rgba(255, 255, 255, 0.769);
            margin-top: -1em;

            @media (min-width: 1024px) {
                font-size: 20px;
            }
        }

        small {
            color: rgba(255, 255, 255, 0.878);
            margin-top: -1em;
            text-align: center;
            padding: 0 1em;
            font-size: 0.8rem;
            line-height: 1rem;
            margin: .2rem 0 0 0;

            @media (min-width:480px) {
                font-size: 11.8px;
            }

            @media (min-width:1024px) {
                font-size: 13.8px;
            }
        }

        a {

            button {
                position: relative;
                margin-top: 2.5em;
                padding: 0.6em 1.8em;
                border-radius: 30px;
                border: 1px solid #00ddff2c;
                background: #00ddff47;
                box-shadow: 0px 1px 8px 0.2px #0000004d;
                color: #ffffff;
                transition: all 0.4s ease;
            }
        }

        button:hover {
            transform: scale(1.04);
            transition: all 0.4s ease;
            background: rgba(114, 196, 255, 0.654);
            border: 0.1px solid rgba(58, 91, 238, 0.327);
            box-shadow: 0px 1px 8px 0.1px #00000068;
            color: rgb(255, 255, 255);
        }
    }
}

//**************** OUR-SERVICES ****************
//**********************************************
.our-services {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: linear-gradient(182deg, rgb(9, 9, 9) 50%, #545454 100%);

    h1 {
        color: #ffffffe5;
        font-size: 2em;
        font-weight: bold;
        margin-top: 0em;

        @media (min-width:1024px) {
            font-size: 40px;
        }

        span {
            color: #00d5ff;
        }
    }

    //*************** SLIDER-CONTAINER *********************
    //*******************************************************
    >article {
        width: 85%;
        height: 18em;

        @media (min-width:480px) {
            width: 55%;
        }

        @media (min-width:1024px) {
            width: 85%;
        }

        .main-carousel {
            width: 100%;
            padding: 0 1em;


            .flickity-viewport {
                width: 100%;

                .flickity-slider {
                    width: 100%;

                    .carousel-cell {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: calc(100%);
                        height: 260px;
                        margin: 0 2em;

                        @media (min-width: 480px) {
                            height: 355px;
                        }

                        @media (min-width:1024px) {
                            width: 35%;
                            height: 260px;
                        }

                        // ( > ) targeting the first child
                        >article {
                            width: 90%;
                            height: 90%;
                            position: relative;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            padding: 1em;
                            background: rgb(0, 0, 0);
                            border-radius: 5px;
                            box-shadow: 0 0 0.9px 1px black;

                            &:before,
                            &:after {
                                content: "";
                                position: absolute;
                                width: 2px;
                                height: 2px;
                                border-radius: 100%;
                                top: 5px;
                                right: 5px;
                                background: linear-gradient(140deg, rgba(255, 255, 255, 0.136) 0%, rgba(255, 255, 255, 0.096) 25%);
                                z-index: -1;
                                transition: all 0.4s ease;
                                box-shadow: 0 0 0.9px 1px rgba(27, 255, 1, 0.572);
                            }

                            &:hover {

                                &:before,
                                &:after {
                                    top: -8px;
                                    right: -8px;
                                    width: 20px;
                                    height: 20px;
                                    background: rgba(27, 255, 1, 0.572);
                                    transition: all 0.7s ease;
                                    z-index: 1;
                                }
                            }

                            h1 {
                                font-size: 1.1em;
                                color: #00d5ffc1;
                                text-align: center;
                                line-height: 1.2em;
                            }

                            small {
                                color: #ffffffcb;
                                font-size: 0.9em;
                                font-family: 'Poppins', sans-serif;
                                text-align: center;
                                line-height: 1.2em;
                            }
                        }
                    }
                }
            }

            .flickity-button {
                display: flex;
                margin-left: -2em;
                margin-right: -2em;
                background: #7db2ba6f;
            }

            .flickity-button:hover {
                background: #8ff0ff6f;
            }

            .flickity-page-dots {
                margin-left: -1em;
            }
        }
    }

    //*********** SCROLL-ARROW *************
    //**************************************
    a {
        div {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 2em;
            height: 2em;
            border-radius: 100%;
            margin-left: -1em;
            margin-top: 3.9em;
            cursor: pointer;
            background: #00ddff47;
            box-shadow: 0px 1px 8px 0.2px #0000004d;
            color: #00dcff;
            transition: all 0.4s ease;

            i {
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;
                text-decoration: none;
                color: #19b9dd;
                transition: all 0.3s ease;
                width: 100%;
                padding: 0.5em;
            }
        }

        div:hover {
            transition: all 0.3s ease;
            background: rgba(114, 196, 255, 0.654);
            border: 0.1px solid rgba(58, 91, 238, 0.327);
            box-shadow: 0px 1px 8px 0.1px #00000068;
        }

        i:hover {
            color: white;
        }
    }
}

//************* SERVICE-INFORMATION **************
//************************************************
.service-information {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(0deg, rgb(7, 7, 7) 25%, rgb(164, 164, 164) 200%);
    width: 100%;
    padding: 0 15%;

    @media (min-width:320px) {
        padding: 0 1%;
    }

    h1 {
        top: 0em;

        @media (min-width: 480px) {
            top: 0;
        }

        @media (min-width: 1024px) {
            font-size: 45px;
            top: 0em;
        }

        span {
            color: #D3F3F8;
        }

        position: relative;
        color: #00DCFF;
        font-size: 1.8em;
        font-weight: bold;
    }

    //******************* SERVICE-INFORMATION-CARD-CONTAINER *********************
    //****************************************************************************
    .service-information-cards-container {
        display: flex;
        flex-direction: column;
        width: 100%;

        @media (min-width: 768px) {
            flex-direction: column;
            width: 100%;
            justify-content: center;
            align-items: center;
        }

        @media (min-width:1024px) {
            flex-direction: column;
            padding: 5em;
            margin-left: 1em;
        }

        >div {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            padding: 0 .5rem;
            margin: 2rem 0 0 0;

            @media(min-width: 768px) {
                margin-left: 0;
                flex-direction: row;
            }

            >article {
                display: flex;
                flex-direction: row;
                width: 100%;

                @media (min-width:1024px) {
                    margin-left: 3.5em;
                }

                figure {
                    background-position: center center;
                    background-repeat: no-repeat;
                    background-size: cover;
                    min-width: 4em;
                    height: 4em;
                    border-radius: 100%;
                    margin: 0 1rem 0 0;

                    @media (min-width: 768px) {
                        min-width: 5em;
                        height: 5em;
                    }
                }

                >div {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    width: 100%;

                    h2 {
                        color: #00DCFF;
                        font-size: 1em;
                        line-height: 1em;
                        margin-bottom: 1em;

                        @media (min-width:768px) {
                            font-size: 1.2em;
                        }

                        @media (min-width:1024px) {
                            font-size: 1.5em;
                        }
                    }

                    p {
                        line-height: 1.2em;
                        color: #D3F3F8;
                        margin-top: -0.8em;

                        @media (min-width:480px) {
                            padding: 0 2em 0 0;
                        }

                        @media (min-width:768px) {
                            font-size: 1em;
                        }

                        @media (min-width:1024px) {
                            font-size: 1.0em;
                            padding: 0 2em 0 0;
                        }
                    }
                }
            }

            &:nth-of-type(1) {
                margin: 0;

                article {

                    figure {
                        background-image: url("../images/thumb-img-1.jpg");
                    }

                    &:nth-of-type(2) {
                        figure {
                            background-image: url("../images/thumb-img-2.jpg");
                        }
                    }
                }
            }

            &:nth-of-type(2) {
                margin: 0;

                article {

                    figure {
                        background-image: url("../images/thumb-img-3.jpg");
                    }

                    &:nth-of-type(2) {
                        figure {
                            background-image: url("../images/thumb-img-4.jpg");
                        }
                    }
                }
            }
        }
    }

    //*********** GET-QUOTE **************
    //************************************
    >div {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0.5em;

        small {
            display: flex;
            flex-direction: column;
            color: #00DCFF;
            font-size: 0.4em;

            @media (min-width: 320px) {
                display: none;
            }
        }

        span {
            position: relative;
            color: #91effd;
            font-size: 0.4em;
            letter-spacing: 0.1em;

            @media (min-width: 320px) {
                display: none;
            }
        }

        a {
            button {
                position: relative;
                text-decoration: none;
                font-size: 0.8em;
                margin-top: .5em;
                padding: 0.6em 1.8em;
                border-radius: 30px;
                border: 1px solid #00ddff2c;
                background: #00ddff47;
                box-shadow: 0px 1px 8px 0.2px #0000004d;
                transition: all 0.4s ease;
                color: #00d5ff;

                @media (min-width:320px) {
                    margin-top: 2em;
                }

                @media (min-width: 480px) {
                    margin-top: 3em;
                }

                @media (min-width: 768px) {
                    margin-top: 4em;
                }

                @media (min-width:1024px) {
                    margin-top: 5em;
                }
            }
        }

        button:hover {
            transform: scale(1.04);
            transition: all 0.4s ease;
            background: rgba(114, 196, 255, 0.654);
            border: 0.1px solid rgba(58, 91, 238, 0.327);
            box-shadow: 0px 1px 8px 0.1px #00000068;
            color: white;
        }
    }
}

//***************** ABOUT-US ******************
//*********************************************
.about-us {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.762), rgba(0, 0, 0, 0.47)), url("../images/about-us.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    overflow: hidden;
    scroll-behavior: smooth;
    width: 100%;
    height: 100vh;
    padding: 2em;

    h1 {
        font-size: 2em;
        color: #00DCFF;
        top: 0;

        @media (min-width:1024px) {
            font-size: 45px;
        }

        span {
            color: #D3F3F8;
        }
    }

    >div {
        margin-bottom: 2.5em;

        p {
            position: relative;
            display: flex;
            flex-direction: column;
            color: #D3F3F8;
            text-align: center;
            max-width: 45em;

            @media (min-width:1024px) {
                font-size: 1.2em;

            }

            span {
                font-size: 0.75em;
                font-weight: bold;
                letter-spacing: 1.2px;
            }
        }
    }

    >div {
        position: relative;
    }

    button {
        position: relative;
        margin-top: 2.5em;
        padding: 0.6em 1.8em;
        border-radius: 30px;
        border: 1px solid #00ddff2c;
        background: #00ddff47;
        box-shadow: 0px 1px 8px 0.2px #0000004d;
        transition: all 0.4s ease;
        color: #00d5ff;
    }
}

button:hover {
    transform: scale(1.04);
    transition: all 0.4s ease;
    background: rgba(114, 196, 255, 0.654);
    border: 0.1px solid rgba(58, 91, 238, 0.327);
    box-shadow: 0px 1px 8px 0.1px #00000068;
    color: white;
}

//**************** CONTACT *********************
//**********************************************
.contact {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(180deg, rgb(15, 15, 15) 50%, rgb(69, 69, 69) 100%);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    scroll-behavior: smooth;
    width: 100%;
    height: 100vh;
    padding: 0.5em;

    @media (min-width:768px) {
        flex-direction: row;
    }

    article {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        justify-content: space-between;
        padding: 1em;

        @media (min-width:768px) {
            flex-direction: row;
        }

        >div {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            flex-direction: column;

            @media (min-width:768px) {
                margin-right: 4em;
            }

            @media (min-width:1024px) {
                margin-right: 4em;
            }

            h2 {
                color: #00DCFF;
                font-size: 2em;
                margin-bottom: 2em;
            }

            h4 {
                color: rgb(218, 229, 255);
                margin-top: -2em;
            }

            >a {
                color: rgb(218, 229, 255);
                margin-top: -1em;
                font-size: 1em;
                line-height: 1em;
                text-align: center;
                text-decoration: none;
                margin-top: 1em;
                transition: all 0.4s ease;
            }

            >p {
                color: white;
                margin-top: 0;
            }

            >a:hover {
                transform: scale(1.1);
                transition: all 0.4s ease;
                cursor: pointer;
                color: #00d5ff;
            }
        }

        >div {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            label {
                position: relative;
                display: flex;
                flex-direction: column;
                color: #00DCFF;
                text-align: center;
                font-size: 0.6em;
                margin-bottom: 1em;
            }

            >div {
                position: relative;

                iframe {
                    border-radius: 15px;
                    box-shadow: 0px 1px 8px 0.1px #000000;
                }
            }
        }
    }
}

//************* FOOTER ***************
//************************************
footer {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0.5em;
    background: #1e2729;
    color: #D3F3F8;
    margin-top: -2.1em;

    a {
        text-decoration: none;
        color: #00aaffc6;
        font-weight: bold;
    }
}